import { B as BASIC_EVENT_FILTER } from "../../../../../chunks/events.js";
import dayjs from "dayjs";
const include = {
  author: true,
  comments: {
    include: {
      member: true
    }
  },
  going: true,
  interested: true,
  tags: true
};
const load = async ({ locals, url }) => {
  const { prisma } = locals;
  const view = "month";
  const startDateParam = url.searchParams.get("startDate");
  const endDateParam = url.searchParams.get("endDate");
  const firstDate = startDateParam ? dayjs(startDateParam) : dayjs().startOf(view).subtract(1, "week");
  const lastDate = endDateParam ? dayjs(endDateParam) : dayjs().endOf(view).add(1, "week");
  const events = await prisma.event.findMany({
    where: {
      AND: [
        BASIC_EVENT_FILTER(),
        {
          // capture all events which overlap the range firstDate-lastDate
          OR: [
            {
              startDatetime: {
                gte: firstDate.toDate(),
                lte: lastDate.toDate()
              }
            },
            {
              endDatetime: {
                gte: firstDate.toDate(),
                lte: lastDate.toDate()
              }
            }
          ]
        }
      ]
    },
    include
  });
  return {
    events
  };
};
export {
  load
};
